@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:ital,wght@0,100..900;1,100..900&display=swap');
*{
  font-family: "Roboto Condensed", sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  background-color: #555;
}
.calendar{
  width: 400px;
  background-color: #fff;
  margin: 50px auto;
  box-shadow: 0 0 10px rgba(0, 0,0, 0.1);
  padding: 5px;
}
.header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #3498db;
  color:#fff;
  padding:5px;
}
button{
  border-radius:6px;
  border: none;
  cursor: pointer;
  font-size: 15px;
  color: #fff;
}

select{
  font-size: 18px;
  padding: 0px 8px;
  border: 1px solid #3498db;
  border-radius: 4px;
  margin: 0 10px;
}
.daysofweek{
  display: grid;
  grid-template-columns: repeat(7,1fr);
  text-align: center;
  font-weight: bold;
  padding: 10px;
  background-color: #fff;
  margin-bottom: 10px;
}
.days{
  display: grid;
  grid-template-columns: repeat(7,1fr);
  gap: 9px;
}
.day,
.empty{
  padding: 10px;
  text-align: center;
  border: 1px solid #ccc;
  cursor: pointer;
  transition: 0.5s;
}
.empty{
  background-color: #f9f9f9;
}
.day:hover{
  background-color: #3498db;
  color: #fff;
}
.current{
  background-color: tomato;
  color: #fff;
}